import React, { Component } from 'react';
import { TableData } from 'components/common';
import { Divider, Row, Col } from 'antd';
import {
  getStudentAdditionSubject,
  createRegisterStudentCreditEnrollment,
} from 'services/additionalRegistration';

class TableAddRegisterNotSelected extends Component {
  constructor(props) {
    super(props);
    this.state = { selectedRowKeys: [], selectedRows: [] };
  }

  onSelectChange = (selectedRowKeys, selectedRows) => {
    const { setSubjectSelectedRows } = this.props;
    this.setState({ selectedRowKeys, selectedRows });
    setSubjectSelectedRows(selectedRows);
  };

  columns = [
    {
      title: 'Mã học phần',
      dataIndex: 'syllableCode',
      key: 'syllableCode',
    },
    {
      title: 'Tên học phần',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'Số TC',
      dataIndex: 'noOfCredit',
      key: 'noOfCredit',
    },
    {
      title: 'Tên lớp tín chỉ',
      dataIndex: 'classCreditCode',
      key: 'classCreditCode',
    },
    {
      title: 'Lịch học',
      dataIndex: 'schedule',
      key: 'schedule',
    },
    {
      title: 'Giáo viên',
      dataIndex: 'teacherName',
      key: 'teacherName',
    },
    {
      title: 'Phòng học',
      dataIndex: 'roomName',
      key: 'roomName',
    },
    {
      title: 'Còn trống',
      dataIndex: 'noOfEmptySlot',
      key: 'noOfEmptySlot',
    },
  ];

  handleAdd = async () => {
    const { selectedRows } = this.state;
    const { student, doReload } = this.props;
    const createCCEs = selectedRows.map(selectedRow => ({
      ccsId: selectedRow.ccsId,
    }));
    const reqBody = {
      userLogged: '3fa85f64-5717-4562-b3fc-2c963f66afa6',
      studentId: student.id,
      createCCEs,
    };
    try {
      await createRegisterStudentCreditEnrollment(reqBody);
      this.setState({ selectedRowKeys: [], selectedRows: [] });
      doReload();
    } catch (err) {
      console.error(err);
    }
  };

  render() {
    const { selectedRowKeys } = this.state;
    const { paramSearch, student, reload } = this.props;
    paramSearch.isRegister = false;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <div>
        <br />
        <Row gutter={24}>
          <Col span={4}>
            Hệ: <b>{student.trainType}</b>
          </Col>
          <Col span={4}>
            Khoa: <b>{student.faculty}</b>
          </Col>
          <Col span={4}>
            Khóa: <b>{student.course}</b>
          </Col>
          <Col span={4}>
            CN: <b>{student.studyField}</b>
          </Col>
          <Col span={4}>
            Ngày sinh: <b>{student.dob}</b>
          </Col>
          <Col span={4}>
            Họ tên: <b>{student.fullname}</b>
          </Col>
        </Row>
        <br />
        <Divider>Danh sách môn chưa đăng ký</Divider>
        <TableData
          reload={reload}
          rowSelection={rowSelection}
          service={getStudentAdditionSubject}
          paramSearch={paramSearch}
          columns={this.columns}
          allowGetData={
            !!paramSearch.studentCode &&
            // !!paramSearch.studyFieldId &&
            !!paramSearch.yearEduId &&
            !!paramSearch.semesterId &&
            !!paramSearch.registerSessionId
          }
        />
      </div>
    );
  }
}

export default TableAddRegisterNotSelected;



